package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCAlignment
import com.sefagurel.dynamiccomponent.sharedmodel.DCArrangement
import com.sefagurel.dynamiccomponent.sharedmodel.DCHorizontalArrangement
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCRowDCHorizontalArrangement(
    modifier: Modifier = Modifier,
    arrangement: DCHorizontalArrangement?,
    onSelected: (DCHorizontalArrangement?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Start" to DCArrangement.Start,
        "End" to DCArrangement.End,
        "SpacedByWithHorizontalAlignment"
                to DCArrangement.SpacedByWithHorizontalAlignment(0f, DCAlignment.Start),
        "AlignedHorizontal" to DCArrangement.AlignedHorizontal(DCAlignment.Start)
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = arrangement,
        onSelected = onSelected
    )
}