package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCAlignment
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCBoxDCAlignment(
    modifier: Modifier = Modifier,
    alignment: DCAlignment?,
    onSelected: (DCAlignment?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "TopStart" to DCAlignment.TopStart,
        "TopCenter" to DCAlignment.TopCenter,
        "TopEnd" to DCAlignment.TopEnd,
        "CenterStart" to DCAlignment.CenterStart,
        "Center" to DCAlignment.Center,
        "CenterEnd" to DCAlignment.CenterEnd,
        "BottomStart" to DCAlignment.BottomStart,
        "BottomCenter" to DCAlignment.BottomCenter,
        "BottomEnd" to DCAlignment.BottomEnd,
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = alignment,
        onSelected = onSelected
    )
}