internal fun DCComponent.toUIModel(): ComponentUIModel {
    return when (this) {
        is DCBox -> this.toUIModel()
        is DCColumn -> this.toUIModel()
        is DCLazyColumn -> this.toUIModel()
        is DCRow -> this.toUIModel()
        is DCLazyRow -> this.toUIModel()
        is DCText -> this.toUIModel()
        is DCImage -> this.toUIModel()
    }
}

@Composable
internal fun List<Any>.createModifiers(scope: Any): Modifier {
    var modifier = Modifier.init()
    this.forEach {
        when (it) {
            is Modifier -> {
                modifier = modifier.then(it)
            }

            is DCModifier.VerticalScroll -> {
                val vs = it.convertVerticalScroll()
                modifier = modifier.then(vs)
            }

            is DCModifier.HorizontalScroll -> {
                val hs = it.convertHorizontalScroll()
                modifier = modifier.then(hs)
            }

            is DCModifier.Weight -> {
                val wm = it.convertWeight(scope)
                modifier = modifier.then(wm)
            }
        }
    }
    return modifier
}

private fun DCBox.toUIModel(): BoxUIModel {
    return BoxUIModel(
        modifier = modifiers?.toUIModel() ?: emptyList(),
        contentAlignment = contentAlignment?.toUIModel() ?: Alignment.TopStart,
        propagateMinConstraints = propagateMinConstraints ?: false,
        children = componentList?.map { it.toUIModel() } ?: emptyList()
    )
}

private fun DCColumn.toUIModel(): ColumnUIModel {
    return ColumnUIModel(
        modifier = modifiers?.toUIModel() ?: emptyList(),
        verticalArrangement = verticalArrangement?.toUIModel() ?: Arrangement.Top,
        horizontalAlignment = horizontalAlignment?.toUIModel() ?: Alignment.Start,
        children = componentList?.map { it.toUIModel() } ?: emptyList()
    )
}

private fun DCLazyColumn.toUIModel(): LazyColumnUIModel {
    return LazyColumnUIModel(
        modifier = modifiers?.toUIModel() ?: emptyList(),
        verticalArrangement = verticalArrangement?.toUIModel() ?: Arrangement.Top,
        horizontalAlignment = horizontalAlignment?.toUIModel() ?: Alignment.Start,
        children = componentList?.map { it.toUIModel() } ?: emptyList()
    )
}

private fun DCRow.toUIModel(): RowUIModel {
    return RowUIModel(
        modifier = modifiers?.toUIModel() ?: emptyList(),
        horizontalArrangement = horizontalArrangement?.toUIModel() ?: Arrangement.Start,
        verticalAlignment = verticalAlignment?.toUIModel() ?: Alignment.Top,
        children = componentList?.map { it.toUIModel() } ?: emptyList()
    )
}

private fun DCLazyRow.toUIModel(): LazyRowUIModel {
    return LazyRowUIModel(
        modifier = modifiers?.toUIModel() ?: emptyList(),
        horizontalArrangement = horizontalArrangement?.toUIModel() ?: Arrangement.Start,
        verticalAlignment = verticalAlignment?.toUIModel() ?: Alignment.Top,
        children = componentList?.map { it.toUIModel() } ?: emptyList()
    )
}

private fun DCText.toUIModel(): TextUIModel {
    return TextUIModel(
        text = text.orEmpty(),
        modifier = modifiers?.toUIModel() ?: emptyList(),
        color = color?.toColor() ?: Color.Unspecified,
        fontSize = fontSize?.sp ?: TextUnit.Unspecified,
        fontStyle = fontStyle.toUIModel(),
        fontWeight = fontWeight?.let { FontWeight(it) },
        fontFamily = fontFamily.toUIModel(),
        letterSpacing = letterSpacing?.sp ?: TextUnit.Unspecified,
        textDecoration = textDecoration.toUIModel(),
        textAlign = textAlign.toUIModel(),
        lineHeight = lineHeight?.sp ?: TextUnit.Unspecified,
        overflow = overflow?.toUIModel() ?: TextOverflow.Clip,
        softWrap = softWrap ?: true,
        maxLines = maxLines ?: Int.MAX_VALUE,
        minLines = minLines ?: 1
    )
}

private fun DCImage.toUIModel(): ImageUIModel {
    return ImageUIModel(
        url = url.orEmpty(),
        contentDescription = contentDescription,
        modifier = modifiers?.toUIModel() ?: emptyList(),
        alignment = alignment?.toUIModel() ?: Alignment.Center,
        contentScale = contentScale?.toUIModel() ?: ContentScale.Fit,
        alpha = alpha ?: 1.0f
    )
}

private fun DCAlignment.toUIModel(): Alignment {
    return when (this) {
        DCAlignment.BottomCenter -> Alignment.BottomCenter
        DCAlignment.BottomEnd -> Alignment.BottomEnd
        DCAlignment.BottomStart -> Alignment.BottomStart
        DCAlignment.Center -> Alignment.Center
        DCAlignment.CenterEnd -> Alignment.CenterEnd
        DCAlignment.CenterStart -> Alignment.CenterStart
        DCAlignment.TopCenter -> Alignment.TopCenter
        DCAlignment.TopEnd -> Alignment.TopEnd
        DCAlignment.TopStart -> Alignment.TopStart
    }
}

private fun DCVerticalAlignment.toUIModel(): Alignment.Vertical {
    return when (this) {
        DCAlignment.Bottom -> Alignment.Bottom
        DCAlignment.CenterVertically -> Alignment.CenterVertically
        DCAlignment.Top -> Alignment.Top
    }
}

private fun DCHorizontalAlignment.toUIModel(): Alignment.Horizontal {
    return when (this) {
        DCAlignment.CenterHorizontally -> Alignment.CenterHorizontally
        DCAlignment.End -> Alignment.End
        DCAlignment.Start -> Alignment.Start
    }
}

private fun DCVerticalArrangement.toUIModel(): Arrangement.Vertical? {
    return when (this) {
        is DCArrangement.AlignedVertical -> {
            val alignment = this.alignment?.toUIModel() ?: return null
            Arrangement.aligned(alignment)
        }

        DCArrangement.Bottom -> Arrangement.Bottom
        DCArrangement.Center -> Arrangement.Center
        DCArrangement.SpaceAround -> Arrangement.SpaceAround
        DCArrangement.SpaceBetween -> Arrangement.SpaceBetween
        DCArrangement.SpaceEvenly -> Arrangement.SpaceEvenly
        is DCArrangement.SpacedBy -> {
            val space = this.space?.dp ?: return null
            Arrangement.spacedBy(space)
        }

        is DCArrangement.SpacedByWithVerticalAlignment -> {
            val space = this.space?.dp ?: return null
            val alignment = this.alignment?.toUIModel() ?: return null
            Arrangement.spacedBy(space, alignment)
        }

        DCArrangement.Top -> Arrangement.Top
    }
}

private fun DCHorizontalArrangement.toUIModel(): Arrangement.Horizontal? {
    return when (this) {
        is DCArrangement.AlignedHorizontal -> {
            val alignment = this.alignment?.toUIModel() ?: return null
            Arrangement.aligned(alignment)
        }

        DCArrangement.End -> Arrangement.End
        DCArrangement.Center -> Arrangement.Center
        DCArrangement.SpaceAround -> Arrangement.SpaceAround
        DCArrangement.SpaceBetween -> Arrangement.SpaceBetween
        DCArrangement.SpaceEvenly -> Arrangement.SpaceEvenly
        is DCArrangement.SpacedBy -> {
            val space = this.space?.dp ?: return null
            Arrangement.spacedBy(space)
        }

        is DCArrangement.SpacedByWithHorizontalAlignment -> {
            val space = this.space?.dp ?: return null
            val alignment = this.alignment?.toUIModel() ?: return null
            Arrangement.spacedBy(space, alignment)
        }

        DCArrangement.Start -> Arrangement.Start
    }
}

private fun DCHorizontalOrVerticalArrangement.toUIModel(): Arrangement.HorizontalOrVertical? {
    return when (this) {
        DCArrangement.Center -> Arrangement.Center
        DCArrangement.SpaceEvenly -> Arrangement.SpaceEvenly
        DCArrangement.SpaceAround -> Arrangement.SpaceAround
        DCArrangement.SpaceBetween -> Arrangement.SpaceBetween
        is DCArrangement.SpacedBy -> {
            val space = this.space?.dp ?: return null
            Arrangement.spacedBy(space)
        }
    }
}

private fun DCContentScale.toUIModel(): ContentScale {
    return when (this) {
        DCContentScale.Crop -> ContentScale.Crop
        DCContentScale.FillBounds -> ContentScale.FillBounds
        DCContentScale.FillHeight -> ContentScale.FillHeight
        DCContentScale.FillWidth -> ContentScale.FillWidth
        DCContentScale.Fit -> ContentScale.Fit
        DCContentScale.Inside -> ContentScale.Inside
        DCContentScale.None -> ContentScale.None
    }
}

private fun DCFontStyle?.toUIModel(): FontStyle? {
    return when (this) {
        DCFontStyle.Italic -> FontStyle.Italic
        DCFontStyle.Normal -> FontStyle.Normal
        else -> null
    }
}

private fun DCFontFamily?.toUIModel(): FontFamily? {
    return when (this) {
        DCFontFamily.Default -> FontFamily.Default
        DCFontFamily.SansSerif -> FontFamily.SansSerif
        DCFontFamily.Serif -> FontFamily.Serif
        DCFontFamily.Monospace -> FontFamily.Monospace
        DCFontFamily.Cursive -> FontFamily.Cursive
        else -> null
    }
}

private fun DCTextDecoration?.toUIModel(): TextDecoration? {
    return when (this) {
        DCTextDecoration.None -> TextDecoration.None
        DCTextDecoration.Underline -> TextDecoration.Underline
        DCTextDecoration.LineThrough -> TextDecoration.LineThrough
        else -> null
    }
}

private fun DCTextAlign?.toUIModel(): TextAlign? {
    return when (this) {
        DCTextAlign.Left -> TextAlign.Left
        DCTextAlign.Right -> TextAlign.Right
        DCTextAlign.Center -> TextAlign.Center
        DCTextAlign.Justify -> TextAlign.Justify
        DCTextAlign.Start -> TextAlign.Start
        DCTextAlign.End -> TextAlign.End
        else -> null
    }
}

private fun DCTextOverflow.toUIModel(): TextOverflow {
    return when (this) {
        DCTextOverflow.Clip -> TextOverflow.Clip
        DCTextOverflow.Ellipsis -> TextOverflow.Ellipsis
        DCTextOverflow.Visible -> TextOverflow.Visible
    }
}

private fun DCShape.toUIModel(): Shape {
    return when (this) {
        is DCShape.RectangleShape -> RectangleShape
        is DCShape.CircleShape -> CircleShape
        is DCShape.RoundedCornerShape -> RoundedCornerShape(
            this.topStart?.dp ?: 0.dp,
            this.topEnd?.dp ?: 0.dp,
            this.bottomEnd?.dp ?: 0.dp,
            this.bottomStart?.dp ?: 0.dp
        )
    }
}


// Modifiers
private fun List<DCModifier>.toUIModel(): List<Any> {
    val modifier = arrayListOf<Any>()
    this.forEach { dcModifier ->
        dcModifier.toUIModel()?.let {
            modifier += it
        }
    }
    return modifier
}

private fun DCModifier.toUIModel(): Any? {
    return when (this) {
        is DCModifier.AspectRatio -> convertAspectRatio()
        is DCModifier.Background -> convertBackground()
        is DCModifier.Border -> convertBorder()
        is DCModifier.Clip -> convertClip()
        is DCModifier.FillMaxHeight -> convertFillMaxHeight()
        is DCModifier.FillMaxWidth -> convertFillMaxWidth()
        is DCModifier.Height -> convertHeight()
        is DCModifier.HeightIn -> convertHeightIn()
        is DCModifier.Padding -> convertPadding()
        is DCModifier.Shadow -> convertShadow()
        is DCModifier.Weight -> this
        is DCModifier.Width -> convertWidth()
        is DCModifier.WidthIn -> convertWidthIn()
        is DCModifier.VerticalScroll -> this
        is DCModifier.HorizontalScroll -> this
        is DCModifier.Size -> convertSize()
    }
}

private fun DCModifier.Size.convertSize(): Modifier {
    val width = this.width?.dp ?: Dp.Unspecified
    val height = this.height?.dp ?: Dp.Unspecified
    return Modifier.size(width, height)
}

private fun DCModifier.AspectRatio.convertAspectRatio(): Modifier? {
    val ratio = this.ratio ?: return null
    return Modifier.aspectRatio(ratio)
}

private fun DCModifier.Background.convertBackground(): Modifier? {
    val color = this.color.toColor() ?: return null
    val shape = this.shape?.toUIModel() ?: RectangleShape
    return Modifier.background(color, shape)
}

private fun DCModifier.Border.convertBorder(): Modifier? {
    val width = this.width?.dp ?: return null
    val color = this.color.toColor() ?: return null
    val shape = this.shape?.toUIModel() ?: RectangleShape
    return Modifier.border(width, color, shape)
}

private fun DCModifier.Clip.convertClip(): Modifier {
    val shape = this.shape?.toUIModel() ?: RectangleShape
    return Modifier.clip(shape)
}

private fun DCModifier.FillMaxHeight.convertFillMaxHeight(): Modifier {
    return Modifier.fillMaxHeight()
}

private fun DCModifier.FillMaxWidth.convertFillMaxWidth(): Modifier {
    return Modifier.fillMaxWidth()
}

private fun DCModifier.Height.convertHeight(): Modifier {
    val height = this.height?.dp ?: Dp.Unspecified
    return Modifier.height(height)
}

private fun DCModifier.HeightIn.convertHeightIn(): Modifier {
    val min = this.min?.dp ?: Dp.Unspecified
    val max = this.max?.dp ?: Dp.Unspecified
    return Modifier.heightIn(min, max)
}

private fun DCModifier.Padding.convertPadding(): Modifier {
    return Modifier.padding(
        this.start?.dp ?: 0.dp,
        this.top?.dp ?: 0.dp,
        this.end?.dp ?: 0.dp,
        this.bottom?.dp ?: 0.dp
    )
}

private fun DCModifier.Shadow.convertShadow(): Modifier? {
    val elevation = this.elevation?.dp ?: return null
    val shape = this.shape?.toUIModel() ?: RectangleShape
    val clip = this.clip ?: (elevation > 0.dp)
    val ambientColor = this.ambientColor.toColor() ?: DefaultShadowColor
    val spotColor = this.spotColor.toColor() ?: DefaultShadowColor
    return Modifier.shadow(elevation, shape, clip, ambientColor, spotColor)
}

private fun DCModifier.Width.convertWidth(): Modifier {
    val width = this.width ?: return Modifier
    return Modifier.width(width.dp)
}

private fun DCModifier.WidthIn.convertWidthIn(): Modifier {
    val min = this.min?.dp ?: Dp.Unspecified
    val max = this.max?.dp ?: Dp.Unspecified
    return Modifier.widthIn(min, max)
}


private fun DCModifier.Weight.convertWeight(scope: Any? = null): Modifier {
    val weight = this.weight ?: return Modifier
    with(scope) {
        return when (this) {
            is ColumnScope -> {
                Modifier.weight(weight)
            }

            is RowScope -> {
                Modifier.weight(weight)
            }

            else -> {
                Modifier
            }
        }
    }
}

@Composable
private fun DCModifier.VerticalScroll.convertVerticalScroll(): Modifier {
    return Modifier.verticalScroll(
        state = rememberScrollState(),
        enabled = this.enabled ?: true,
        reverseScrolling = this.reverseScrolling ?: false
    )
}

@Composable
private fun DCModifier.HorizontalScroll.convertHorizontalScroll(): Modifier {
    return Modifier.horizontalScroll(
        state = rememberScrollState(),
        enabled = this.enabled ?: true,
        reverseScrolling = this.reverseScrolling ?: false
    )
}