package com.sefagurel.dynamiccomponent.sharedmodel

import kotlinx.serialization.json.Json
import kotlinx.serialization.modules.SerializersModule
import kotlinx.serialization.modules.polymorphic
import kotlinx.serialization.modules.subclass

private val componentSerializersModule = SerializersModule {
    polymorphic(DCComponent::class) {
        subclass(DCBox::class)
        subclass(DCColumn::class)
        subclass(DCLazyColumn::class)
        subclass(DCRow::class)
        subclass(DCLazyRow::class)
        subclass(DCText::class)
        subclass(DCImage::class)
    }
}

val jsonKt = Json {
    encodeDefaults = false
    ignoreUnknownKeys = true
    coerceInputValues = true
    serializersModule = componentSerializersModule
}