package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCTextDCFontWeight(
    modifier: Modifier = Modifier,
    value: Int?,
    onSelected: (Int?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Thin (100)" to 100,
        "ExtraLight (200)" to 200,
        "Light (300)" to 300,
        "Normal (400)" to 400,
        "Medium (500)" to 500,
        "SemiBold (600)" to 600,
        "Bold (700)" to 700,
        "ExtraBold (800)" to 800,
        "Black (900)" to 900
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = value,
        onSelected = onSelected
    )
}