package com.sefagurel.dynamiccomponent.webpanel.component.settings.main

import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.width
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.sharedmodel.DCRow
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.TitleUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param.DCRowDCHorizontalArrangement
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param.DCRowDCVerticalAlignment

@Composable
fun RowUI(
    node: TreeNode?,
    onNodeChanged: (TreeNode?) -> Unit
) {
    if (node == null) return

    val component: DCRow? = remember(node) { node.component as? DCRow }

    Column(
        modifier = Modifier
            .fillMaxWidth()
    ) {

        Row {
            Column(
                modifier = Modifier.weight(1f)
            ) {
                TitleUI("Horizontal Arrangement")

                DCRowDCHorizontalArrangement(
                    arrangement = component?.horizontalArrangement,
                    onSelected = {
                        val updatedNode =
                            node.copy(component = component?.copy(horizontalArrangement = it))
                        onNodeChanged.invoke(updatedNode)
                    }
                )
            }
            Spacer(modifier = Modifier.width(16.dp))
            Column(
                modifier = Modifier.weight(1f)
            ) {
                TitleUI("Vertical Alignment")

                DCRowDCVerticalAlignment(
                    alignment = component?.verticalAlignment,
                    onSelected = {
                        val updatedNode =
                            node.copy(component = component?.copy(verticalAlignment = it))
                        onNodeChanged.invoke(updatedNode)
                    }
                )
            }
        }
    }
}