package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCTextOverflow
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCTextDCTextOverflow(
    modifier: Modifier = Modifier,
    value: DCTextOverflow?,
    onSelected: (DCTextOverflow?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Clip" to DCTextOverflow.Clip,
        "Ellipsis" to DCTextOverflow.Ellipsis,
        "Visible" to DCTextOverflow.Visible
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = value,
        onSelected = onSelected
    )
}