@Composable
internal fun DynamicComponent(
    json: String?,
    modifier: Modifier = Modifier
) {
    val coroutineScope = rememberCoroutineScope()
    var component by remember { mutableStateOf<ComponentUIModel?>(null) }

    LaunchedEffect(key1 = json) {
        coroutineScope.launch {
            component = runCatching {
                json?.parse<DCComponent?>()
            }.getOrNull()?.toUIModel()
        }
    }

    Box(
        modifier = modifier
    ) {
        component?.let {
            DynamicComponent(it)
        } ?: run {
            Loading()
        }
    }

}

@Composable
private fun BoxScope.Loading() {
    CircularProgressIndicator(
        modifier = Modifier
            .padding(32.dp)
            .align(Alignment.Center)
    )
}

@Composable
private fun DynamicComponent(
    model: ComponentUIModel,
    scope: Any = Any()
) {
    when (model) {
        is BoxUIModel -> BoxComponent(model, scope)
        is ColumnUIModel -> ColumnComponent(model, scope)
        is LazyColumnUIModel -> LazyColumnComponent(model, scope)
        is RowUIModel -> RowComponent(model, scope)
        is LazyRowUIModel -> LazyRowComponent(model, scope)
        is TextUIModel -> TextComponent(model, scope)
        is ImageUIModel -> ImageComponent(model, scope)
    }
}

@Composable
private fun BoxComponent(
    model: BoxUIModel,
    scope: Any = Any()
) {
    Box(
        modifier = model.modifier.createModifiers(scope),
        contentAlignment = model.contentAlignment,
        propagateMinConstraints = model.propagateMinConstraints,
        content = {
            model.children.forEach {
                DynamicComponent(it, this)
            }
        }
    )
}

@Composable
private fun ColumnComponent(
    model: ColumnUIModel,
    scope: Any = Any()
) {
    Column(
        modifier = model.modifier.createModifiers(scope),
        verticalArrangement = model.verticalArrangement,
        horizontalAlignment = model.horizontalAlignment,
        content = {
            model.children.forEach {
                DynamicComponent(it, this)
            }
        }
    )
}

@Composable
private fun LazyColumnComponent(
    model: LazyColumnUIModel,
    scope: Any = Any()
) {
    LazyColumn(
        modifier = model.modifier.createModifiers(scope),
        verticalArrangement = model.verticalArrangement,
        horizontalAlignment = model.horizontalAlignment,
        content = {
            model.children.forEach {
                item {
                    DynamicComponent(it, this)
                }
            }
        }
    )
}

@Composable
private fun RowComponent(
