package com.sefagurel.dynamiccomponent.webpanel.data

import com.sefagurel.dynamiccomponent.sharedmodel.DCModifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCShape
import kotlinx.serialization.Serializable


@Serializable
data class Modifier(
    val modifiers: MutableList<DCModifier> = mutableListOf()
)

fun Modifier.then(other: DCModifier): Modifier {
    modifiers += other
    return this
}

fun Modifier.padding(
    start: Float = 0f,
    top: Float = 0f,
    end: Float = 0f,
    bottom: Float = 0f
): Modifier {
    modifiers += DCModifier.Padding(
        start = start,
        top = top,
        end = end,
        bottom = bottom
    )
    return this
}

fun Modifier.padding(
    all: Float = 0f
): Modifier {
    modifiers += DCModifier.Padding(
        start = all,
        top = all,
        end = all,
        bottom = all
    )
    return this
}

fun Modifier.padding(
    vertical: Float = 0f,
    horizontal: Float = 0f
): Modifier {
    modifiers += DCModifier.Padding(
        start = horizontal,
        top = vertical,
        end = horizontal,
        bottom = vertical
    )
    return this
}

fun Modifier.shadow(
    elevation: Float,
    shape: DCShape = DCShape.RectangleShape,
    clip: Boolean = elevation > 0f,
    ambientColor: DCColor = DCColor.Black,
    spotColor: DCColor = DCColor.Black,
): Modifier {
    modifiers += DCModifier.Shadow(
        elevation = elevation,
        shape = shape,
        clip = clip,
        ambientColor = ambientColor.value,
        spotColor = spotColor.value
    )
    return this
}

fun Modifier.widthIn(
    min: Float? = null,
    max: Float? = null
): Modifier {
    modifiers += DCModifier.WidthIn(
        min = min,
        max = max
    )
    return this
}

fun Modifier.width(width: Float): Modifier {
    modifiers += DCModifier.Width(width)
    return this
}

fun Modifier.fillMaxWidth(): Modifier {
    modifiers += DCModifier.FillMaxWidth
    return this
}

fun Modifier.heightIn(
    min: Float? = null,
    max: Float? = null
): Modifier {
    modifiers += DCModifier.HeightIn(
        min = min,
        max = max
    )
    return this
}

fun Modifier.height(height: Float): Modifier {
    modifiers += DCModifier.Height(height)
    return this
}

fun Modifier.fillMaxHeight(): Modifier {
    modifiers += DCModifier.FillMaxHeight
    return this
}

fun Modifier.weight(weight: Float): Modifier {
    modifiers += DCModifier.Weight(weight)
    return this
}

fun Modifier.aspectRatio(
    ratio: Float,
    matchHeightConstraintsFirst: Boolean = false
): Modifier {
    modifiers += DCModifier.AspectRatio(
        ratio = ratio,
        matchHeightConstraintsFirst = matchHeightConstraintsFirst
    )
    return this
}

fun Modifier.clip(shape: DCShape): Modifier {
    modifiers += DCModifier.Clip(shape)
    return this
}

fun Modifier.border(
    width: Float,
    color: DCColor,
    shape: DCShape = DCShape.RectangleShape
): Modifier {
    modifiers += DCModifier.Border(
        width = width,
        color = color.value,
        shape = shape
    )
    return this
}

fun Modifier.background(
    color: DCColor,
    shape: DCShape = DCShape.RectangleShape
): Modifier {
    modifiers += DCModifier.Background(
        color = color.value,
        shape = shape
    )
    return this
}

fun Modifier.verticalScroll(
    enabled: Boolean = true,
    reverseScrolling: Boolean = false
): Modifier {
    modifiers += DCModifier.VerticalScroll(
        enabled = enabled,
        reverseScrolling = reverseScrolling
    )
    return this
}

fun Modifier.horizontalScroll(
    enabled: Boolean = true,
    reverseScrolling: Boolean = false
): Modifier {
    modifiers += DCModifier.HorizontalScroll(
        enabled = enabled,
        reverseScrolling = reverseScrolling
    )
    return this
}

fun Modifier.size(
    size: Float
): Modifier {
    modifiers += DCModifier.Size(
        width = size,
        height = size
    )
    return this
}

fun Modifier.size(
    width: Float,
    height: Float
): Modifier {
    modifiers += DCModifier.Size(
        width = width,
        height = height
    )
    return this
}