package com.sefagurel.dynamiccomponent.webpanel.ext

import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.rounded.CheckBoxOutlineBlank
import androidx.compose.material.icons.rounded.Image
import androidx.compose.material.icons.rounded.TableRows
import androidx.compose.material.icons.rounded.TextFields
import androidx.compose.material.icons.rounded.ViewWeek
import androidx.compose.runtime.snapshots.SnapshotStateList
import androidx.compose.runtime.toMutableStateList
import androidx.compose.ui.graphics.vector.ImageVector
import com.sefagurel.dynamiccomponent.sharedmodel.DCBox
import com.sefagurel.dynamiccomponent.sharedmodel.DCColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCComponent
import com.sefagurel.dynamiccomponent.sharedmodel.DCImage
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCText
import com.sefagurel.dynamiccomponent.webpanel.component.DCComponentModifier
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode

fun TreeNode.toDCComponent(): DCComponent? {
    val childComponents = children?.mapNotNull { it.toDCComponent() }

    return when (val comp = component) {
        is DCBox -> {
            comp.copy(componentList = childComponents, modifiers = modifiers?.map { it.modifier })
        }

        is DCColumn -> {
            comp.copy(componentList = childComponents, modifiers = modifiers?.map { it.modifier })
        }

        is DCLazyColumn -> {
            comp.copy(componentList = childComponents, modifiers = modifiers?.map { it.modifier })
        }

        is DCRow -> {
            comp.copy(componentList = childComponents, modifiers = modifiers?.map { it.modifier })
        }

        is DCLazyRow -> {
            comp.copy(componentList = childComponents, modifiers = modifiers?.map { it.modifier })
        }

        is DCImage -> {
            comp.copy(modifiers = modifiers?.map { it.modifier })
        }

        is DCText -> {
            comp.copy(modifiers = modifiers?.map { it.modifier })
        }

        else -> comp
    }
}

fun DCComponent.toTreeNode(parentId: String? = null): TreeNode {

    var id = getRandomString(5)
    if (parentId != null) id = "${parentId}.${id}"

    var icon: ImageVector? = null
    var childNodes: SnapshotStateList<TreeNode>? = null
    var name = "Unknown"
    var isContainer = true

    val iconType = Icons.Rounded

    when (this) {
        is DCBox -> { // Crop Square
            childNodes = componentList?.map { it.toTreeNode(id) }?.toMutableStateList()
            icon = iconType.CheckBoxOutlineBlank
            name = "Box"
        }

        is DCColumn -> {
            childNodes = componentList?.map { it.toTreeNode(id) }?.toMutableStateList()
            icon = iconType.TableRows
            name = "Column"
        }

        is DCLazyColumn -> {
            childNodes = componentList?.map { it.toTreeNode(id) }?.toMutableStateList()
            icon = iconType.TableRows
            name = "LazyColumn"
        }

        is DCRow -> {
            childNodes = componentList?.map { it.toTreeNode(id) }?.toMutableStateList()
            icon = iconType.ViewWeek
            name = "Row"
        }

        is DCLazyRow -> {
            childNodes = componentList?.map { it.toTreeNode(id) }?.toMutableStateList()
            icon = iconType.ViewWeek
            name = "LazyRow"
        }

        is DCImage -> {
            icon = iconType.Image
            name = "Image | ${this.url.toString()}"
            isContainer = false
        }

        is DCText -> {
            icon = iconType.TextFields
            name = "Text | ${this.text.toString()}"
            isContainer = false
        }
    }

    return TreeNode(
        id = id,
        parentId = parentId,
        icon = icon,
        name = name,
        isContainer = isContainer,
        component = this,
        children = childNodes,
        modifiers = modifiers?.map { DCComponentModifier(getRandomString(), it) }
    )
}