package com.sefagurel.dynamiccomponent.webpanel.component.settings

import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.sharedmodel.DCBox
import com.sefagurel.dynamiccomponent.sharedmodel.DCColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCImage
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCText
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.BoxUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.ColumnUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.ImageUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.LazyColumnUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.LazyRowUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.RowUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.main.TextUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DCComponentDropdown
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.TitleUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.modifier.ModifierUI

@Composable
fun SettingsUI(
    modifier: Modifier = Modifier,
    node: TreeNode?,
    onNodeChanged: (TreeNode?) -> Unit
) {
    if (node == null) return

    Column(
        modifier = modifier
    ) {

        DCComponentDropdown(
            node = node,
            onNodeChanged = onNodeChanged
        )

        Column(
            modifier = Modifier
                .padding(16.dp)
        ) {
            Box(
                modifier = Modifier
            ) {
                when (node.component) {
                    is DCBox -> {
                        BoxUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCColumn -> {
                        ColumnUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCLazyColumn -> {
                        LazyColumnUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCRow -> {
                        RowUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCLazyRow -> {
                        LazyRowUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCText -> {
                        TextUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    is DCImage -> {
                        ImageUI(
                            node = node,
                            onNodeChanged = onNodeChanged
                        )
                    }

                    null -> Unit
                }
            }

            Spacer(modifier = Modifier.height(32.dp))

            TitleUI(
                title = "Modifiers",
                modifier = Modifier
            )

            ModifierUI(
                modifier = Modifier,
                node = node,
                onNodeChanged = onNodeChanged
            )
        }

    }

}