package com.sefagurel.dynamiccomponent.webpanel.component.settings.other

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.rounded.CheckBoxOutlineBlank
import androidx.compose.material.icons.rounded.Image
import androidx.compose.material.icons.rounded.TableRows
import androidx.compose.material.icons.rounded.TextFields
import androidx.compose.material.icons.rounded.ViewWeek
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.sharedmodel.DCBox
import com.sefagurel.dynamiccomponent.sharedmodel.DCColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCComponent
import com.sefagurel.dynamiccomponent.sharedmodel.DCImage
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCText
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode

@Composable
fun DCComponentDropdown(
    node: TreeNode?,
    onNodeChanged: (TreeNode?) -> Unit
) {
    val items = listOf(
        "Box" to ComponentType.Box,
        "Column" to ComponentType.Column,
        "LazyColumn" to ComponentType.LazyColumn,
        "Row" to ComponentType.Row,
        "LazyRow" to ComponentType.LazyRow,
        "Text" to ComponentType.Text,
        "Image" to ComponentType.Image,
    )

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .background(Color.DarkGray)
            .padding(8.dp),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        TitleUI("Component", Color.White)

        DropdownUI(
            modifier = Modifier,
            items = items,
            selectedValue = node?.component?.toEnum(),
            onSelected = {
                onNodeChanged.invoke(it.toComponent(node))
            }
        )
    }
}

private enum class ComponentType {
    Box, Column, LazyColumn, Row, LazyRow, Text, Image
}

private fun DCComponent.toEnum(): ComponentType {
    return when (this) {
        is DCBox -> ComponentType.Box
        is DCColumn -> ComponentType.Column
        is DCImage -> ComponentType.Image
        is DCLazyColumn -> ComponentType.LazyColumn
        is DCLazyRow -> ComponentType.LazyRow
        is DCRow -> ComponentType.Row
        is DCText -> ComponentType.Text
    }
}

private fun ComponentType.toComponent(node: TreeNode?): TreeNode? {
    val iconType = Icons.Rounded
    return when (this) {
        ComponentType.Box -> node?.copy(
            icon = iconType.CheckBoxOutlineBlank,
            name = "Box",
            isContainer = true,
            component = DCBox(modifiers = node.component?.modifiers)
        )

        ComponentType.Column -> node?.copy(
            icon = iconType.TableRows,
            name = "Column",
            isContainer = true,
            component = DCColumn(modifiers = node.component?.modifiers)
        )

        ComponentType.LazyColumn -> node?.copy(
            icon = iconType.TableRows,
            name = "LazyColumn",
            isContainer = true,
            component = DCLazyColumn(modifiers = node.component?.modifiers)
        )

        ComponentType.Row -> node?.copy(
            icon = iconType.ViewWeek,
            name = "Row",
            isContainer = true,
            component = DCRow(modifiers = node.component?.modifiers)
        )

        ComponentType.LazyRow -> node?.copy(
            icon = iconType.ViewWeek,
            name = "LazyRow",
            isContainer = true,
            component = DCLazyRow(modifiers = node.component?.modifiers)
        )

        ComponentType.Text -> node?.copy(
            icon = iconType.TextFields,
            name = "Text",
            isContainer = false,
            component = DCText(modifiers = node.component?.modifiers)
        )

        ComponentType.Image -> node?.copy(
            icon = iconType.Image,
            name = "Image",
            isContainer = false,
            component = DCImage(modifiers = node.component?.modifiers)
        )
    }
}