package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.modifier

import androidx.compose.foundation.layout.padding
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.sharedmodel.DCModifier.Padding
import com.sefagurel.dynamiccomponent.webpanel.component.DCComponentModifier
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode
import com.sefagurel.dynamiccomponent.webpanel.z.component.VerticalReorderList

@Composable
fun ModifierUI(
    modifier: Modifier = Modifier,
    node: TreeNode?,
    onNodeChanged: (TreeNode?) -> Unit
) {

    val modifierList: MutableState<List<DCComponentModifier>> =
        remember { mutableStateOf(node?.modifiers.orEmpty()) }

    LaunchedEffect(node) {
        modifierList.value = node?.modifiers.orEmpty()
    }

    LaunchedEffect(modifierList.value) {
        onNodeChanged.invoke(node?.copy(modifiers = modifierList.value))
    }

    fun updateModifier(updateDCModifier: DCComponentModifier) {
        modifierList.value = modifierList.value.toMutableList().apply {
            val index = indexOfFirst { it.id == updateDCModifier.id }
            if (index != -1) {
                this[index] = updateDCModifier
            }
        }
    }

    VerticalReorderList(
        modifier = modifier,
        items = modifierList,
    ) { item ->

        when (item.modifier) {
//            is AspectRatio -> AspectRatioModifierUI(node = node, item = item)
//            is Background -> BackgroundModifierUI(node = node, item = item)
//            is Border -> BorderModifierUI(node = node, item = item)
//            is Clip -> ClipModifierUI(node = node, item = item)
//            is FillMaxHeight -> FillMaxHeightModifierUI(node = node, item = item)
//            is FillMaxWidth -> FillMaxWidthModifierUI(node = node, item = item)
//            is Height -> HeightModifierUI(node = node, item = item)
//            is HeightIn -> HeightInModifierUI(node = node, item = item)
//            is HorizontalScroll -> HorizontalScrollModifierUI(node = node, item = item)
            is Padding -> PaddingModifierUI(
                dcModifier = item,
                updateDCModifier = ::updateModifier
            )
//            is Shadow -> ShadowModifierUI(node = node, item = item)
//            is Size -> SizeModifierUI(node = node, item = item)
//            is VerticalScroll -> VerticalScrollModifierUI(node = node, item = item)
//            is Weight -> WeightModifierUI(node = node, item = item)
//            is Width -> WidthModifierUI(node = node, item = item)
//            is WidthIn -> WidthInModifierUI(node = node, item = item)
            else -> Text(text = item.id + " - " + item.modifier.toString(), modifier = Modifier.padding(8.dp))
        }

    }

}