package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCTextAlign
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCTextDCTextAlign(
    modifier: Modifier = Modifier,
    value: DCTextAlign?,
    onSelected: (DCTextAlign?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Left" to DCTextAlign.Left,
        "Right" to DCTextAlign.Right,
        "Center" to DCTextAlign.Center,
        "Justify" to DCTextAlign.Justify,
        "Start" to DCTextAlign.Start,
        "End" to DCTextAlign.End
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = value,
        onSelected = onSelected
    )
}