package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCContentScale
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCImageDCContentScale(
    modifier: Modifier = Modifier,
    alignment: DCContentScale?,
    onSelected: (DCContentScale?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Crop" to DCContentScale.Crop,
        "Fit" to DCContentScale.Fit,
        "FillHeight" to DCContentScale.FillHeight,
        "FillWidth" to DCContentScale.FillWidth,
        "Inside" to DCContentScale.Inside,
        "FillBounds" to DCContentScale.FillBounds,
        "None" to DCContentScale.None
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = alignment,
        onSelected = onSelected
    )
}