package com.sefagurel.dynamiccomponent.webpanel.component.settings.other

import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.PaddingValues
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.widthIn
import androidx.compose.material.Icon
import androidx.compose.material.OutlinedButton
import androidx.compose.material.Text
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.filled.ArrowRight
import androidx.compose.material3.DropdownMenu
import androidx.compose.material3.DropdownMenuItem
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp

@Composable
fun <T> DropdownUI(
    modifier: Modifier = Modifier,
    items: List<Pair<String, T>>,
    selectedValue: T? = null,
    onSelected: (T) -> Unit
) {
    var expanded by remember { mutableStateOf(false) }
    var selectedIndex by remember(selectedValue) {
        mutableStateOf(items.indexOfFirst { it.second == selectedValue }.takeIf { it != -1 } ?: 0)
    }

    Box(
        modifier = modifier
    ) {
        OutlinedButton(
            modifier = Modifier.padding(vertical = 8.dp)
                .height(40.dp),
            onClick = {
                expanded = true
            },
            contentPadding = PaddingValues(8.dp)
        ) {
            Text(
                text = items[selectedIndex].first,
                modifier = Modifier.widthIn(min = 200.dp)
            )

            Icon(
                imageVector = Icons.Filled.ArrowRight,
                contentDescription = ""
            )
        }

        DropdownMenu(
            expanded = expanded,
            onDismissRequest = {
                expanded = false
            },
            modifier = Modifier
        ) {
            items.forEachIndexed { index, item ->
                DropdownMenuItem(
                    modifier = Modifier,
                    text = {
                        Text(
                            modifier = Modifier,
                            text = item.first
                        )
                    },
                    onClick = {
                        selectedIndex = index
                        expanded = false
                        onSelected.invoke(item.second)
                    }
                )
            }
        }
    }
}