package com.sefagurel.dynamiccomponent.sharedmodel

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable(DCHorizontalAlignmentSerializer::class)
@SerialName(Sn.HorizontalAlignment)
sealed interface DCHorizontalAlignment

@Serializable(DCVerticalAlignmentSerializer::class)
@SerialName(Sn.VerticalAlignment)
sealed interface DCVerticalAlignment

@Serializable(DCAlignmentSerializer::class)
@SerialName(Sn.Alignment)
sealed interface DCAlignment {

    @Serializable
    @SerialName(Sn.TopStart)
    object TopStart : DCAlignment

    @Serializable
    @SerialName(Sn.TopCenter)
    object TopCenter : DCAlignment

    @Serializable
    @SerialName(Sn.TopEnd)
    object TopEnd : DCAlignment

    @Serializable
    @SerialName(Sn.CenterStart)
    object CenterStart : DCAlignment

    @Serializable
    @SerialName(Sn.Center)
    object Center : DCAlignment

    @Serializable
    @SerialName(Sn.CenterEnd)
    object CenterEnd : DCAlignment

    @Serializable
    @SerialName(Sn.BottomStart)
    object BottomStart : DCAlignment

    @Serializable
    @SerialName(Sn.BottomCenter)
    object BottomCenter : DCAlignment

    @Serializable
    @SerialName(Sn.BottomEnd)
    object BottomEnd : DCAlignment

    // 1D Alignment.Verticals.
    @Serializable
    @SerialName(Sn.Top)
    object Top : DCVerticalAlignment

    @Serializable
    @SerialName(Sn.CenterVertically)
    object CenterVertically : DCVerticalAlignment

    @Serializable
    @SerialName(Sn.Bottom)
    object Bottom : DCVerticalAlignment

    // 1D Alignment.Horizontals.
    @Serializable
    @SerialName(Sn.Start)
    object Start : DCHorizontalAlignment

    @Serializable
    @SerialName(Sn.CenterHorizontally)
    object CenterHorizontally : DCHorizontalAlignment

    @Serializable
    @SerialName(Sn.End)
    object End : DCHorizontalAlignment
}