package com.sefagurel.dynamiccomponent.webpanel

import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.mutableStateListOf
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.DynamicComponentWebPanel
import com.sefagurel.dynamiccomponent.sharedmodel.DCBox
import com.sefagurel.dynamiccomponent.sharedmodel.jsonKt
import com.sefagurel.dynamiccomponent.webpanel.component.ItemAction
import com.sefagurel.dynamiccomponent.webpanel.component.ResizableThreeComponent
import com.sefagurel.dynamiccomponent.webpanel.component.ResizeableWindow
import com.sefagurel.dynamiccomponent.webpanel.component.TreeComponent
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode
import com.sefagurel.dynamiccomponent.webpanel.component.findNodeById
import com.sefagurel.dynamiccomponent.webpanel.component.settings.SettingsUI
import com.sefagurel.dynamiccomponent.webpanel.component.updateNode
import com.sefagurel.dynamiccomponent.webpanel.ext.toDCComponent
import com.sefagurel.dynamiccomponent.webpanel.ext.toTreeNode
import com.sefagurel.dynamiccomponent.webpanel.provider.MessageBox
import com.sefagurel.dynamiccomponent.webpanel.sampledata.uiComponent
import kotlinx.browser.window
import kotlinx.serialization.encodeToString
import org.jetbrains.skiko.wasm.onWasmReady

fun main() {
    onWasmReady {
        ResizeableWindow(window) {
            MainContent()
        }
    }
}

@Composable
fun MainContent() {
    MessageBox {
        val treeNode = remember { mutableStateOf(uiComponent().toTreeNode()) }
        val focusedTreeNode = remember { mutableStateOf<String?>(null) }
        val treeNodeJson = remember { mutableStateOf<String?>(null) }
        val selectedTreeNode = remember { mutableStateOf<TreeNode?>(null) }
        val refreshTree = remember { mutableStateOf(0) }

//        val popup = localDCPopup.current
//
//        LaunchedEffect(treeNodeJson.value) {
//            popup.show(DCMessage("sefa55 -> "+selectedTreeNode.value.toString()))
//        }

        LaunchedEffect(refreshTree.value) {
            treeNodeJson.value = runCatching {
                val newNode = if (focusedTreeNode.value != null) {
                    findNodeById(treeNode.value, focusedTreeNode.value)?.toDCComponent()
                } else {
                    treeNode.value.toDCComponent()
                }
                jsonKt.encodeToString(newNode)
            }.onFailure {
                it.printStackTrace()
            }.getOrNull()
        }



        LaunchedEffect(selectedTreeNode.value) {
            val result = updateNode(treeNode.value, selectedTreeNode.value)
            if (result != null) {
                treeNode.value = result
                refreshTree.value++
            }
        }

        ResizableThreeComponent(
            modifier = Modifier.fillMaxSize(),
            leftComponent = {
                TreeComponent(
                    node = treeNode.value,
                    selectedNode = selectedTreeNode.value,
                    onClick = { itemAction ->

                        when (itemAction) {
                            is ItemAction.Add -> {
                                var clickedNode = itemAction.treeNode

                                val newComp = DCBox(
                                    modifiers = arrayListOf(
//                                        DCModifier.Border(2f, "#ff0000", DCShape.RectangleShape),
//                                        DCModifier.Padding(4f, 4f, 4f, 4f),
//                                        DCModifier.Border(2f, "#0000ff", DCShape.RectangleShape),
//                                        DCModifier.Padding(8f, 8f, 8f, 8f),
//                                        DCModifier.Border(2f, "#008000", DCShape.RectangleShape)
                                    ),
                                ).toTreeNode(clickedNode?.id)

                                if (clickedNode?.children == null) {
                                    clickedNode = clickedNode?.copy(children = mutableStateListOf())
                                }

                                clickedNode?.children?.add(newComp)

                                val updatedTree = updateNode(
                                    tree = treeNode.value,
                                    updatedNode = clickedNode
                                )

                                if (updatedTree != null) {
                                    treeNode.value = updatedTree
                                    selectedTreeNode.value = newComp
                                    refreshTree.value++
                                }
                            }

                            is ItemAction.Remove -> {
                                val clickedNode = itemAction.treeNode

                                val parentNode = findNodeById(treeNode.value, clickedNode?.parentId)

                                parentNode?.children?.removeAll { it.id == clickedNode?.id }

                                val updatedTree = updateNode(
                                    tree = treeNode.value,
                                    updatedNode = parentNode
                                )

                                if (updatedTree != null) {
                                    treeNode.value = updatedTree
                                    selectedTreeNode.value = parentNode
                                    refreshTree.value++
                                }
                            }

                            is ItemAction.Select -> {
                                selectedTreeNode.value = itemAction.treeNode
                            }

                            is ItemAction.Focus -> {
                                if (focusedTreeNode.value == itemAction.treeNode?.id ||
                                    focusedTreeNode.value == treeNode.value.id
                                ) {
                                    focusedTreeNode.value = null
                                    selectedTreeNode.value = treeNode.value
                                } else {
                                    focusedTreeNode.value = itemAction.treeNode?.id
                                    selectedTreeNode.value = itemAction.treeNode
                                }
                                refreshTree.value++
                            }
                        }
                    }
                )
            },
            centerComponent = {
                SettingsUI(
                    node = selectedTreeNode.value,
                    onNodeChanged = {
                        selectedTreeNode.value = it
                    }
                )
            },
            rightComponent = {
                DynamicComponentWebPanel(
                    json = treeNodeJson.value
                )
            },
            dividerColor = Color.Gray,
            dividerWidth = 8.dp
        )
    }
}




