        when (value) {
            is DCArrangement.Center -> encoder.encodeString(Sn.Center)
            is DCArrangement.SpaceEvenly -> encoder.encodeString(Sn.SpaceEvenly)
            is DCArrangement.SpaceBetween -> encoder.encodeString(Sn.SpaceBetween)
            is DCArrangement.SpaceAround -> encoder.encodeString(Sn.SpaceAround)
            is DCArrangement.SpacedBy -> {
                encoder.encodeStructure(descriptor) {
                    encodeNullableSerializableElement(descriptor, 0, Float.serializer(), value.space)
                }
            }
        }
    }

    override fun deserialize(decoder: Decoder): DCHorizontalOrVerticalArrangement {
        val input = decoder as? JsonDecoder ?: throw SerializationException("This class can be loaded only by Json")
