        return when {
            element.jsonPrimitive.content == Sn.Center -> DCArrangement.Center
            element.jsonPrimitive.content == Sn.SpaceEvenly -> DCArrangement.SpaceEvenly
            element.jsonPrimitive.content == Sn.SpaceBetween -> DCArrangement.SpaceBetween
            element.jsonPrimitive.content == Sn.SpaceAround -> DCArrangement.SpaceAround
