    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("DCArrangement", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: DCArrangement) {
        when (value) {
            is DCArrangement.Start -> encoder.encodeString(Sn.Start)
            is DCArrangement.End -> encoder.encodeString(Sn.End)
            is DCArrangement.TopEnd -> encoder.encodeString(Sn.TopEnd)
            is DCArrangement.CenterStart -> encoder.encodeString(Sn.CenterStart)
            is DCArrangement.Top -> encoder.encodeString(Sn.Top)
            is DCArrangement.Bottom -> encoder.encodeString(Sn.Bottom)
        }
    }

    override fun deserialize(decoder: Decoder): DCArrangement {
        val input = decoder as? JsonDecoder ?: throw SerializationException("This class can be loaded only by Json")
        val element = input.decodeJsonElement()

        return when {
            element.jsonPrimitive.content == Sn.Start -> DCArrangement.Start
            element.jsonPrimitive.content == Sn.End -> DCArrangement.End
            element.jsonPrimitive.content == Sn.TopEnd -> DCArrangement.TopEnd
            element.jsonPrimitive.content == Sn.CenterStart -> DCArrangement.CenterStart
            element.jsonPrimitive.content == Sn.Top -> DCArrangement.Top
            element.jsonPrimitive.content == Sn.Bottom -> DCArrangement.Bottom
            else -> throw SerializationException("Unknown value: $element")
        }
    }
}
