package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCAlignment
import com.sefagurel.dynamiccomponent.sharedmodel.DCHorizontalAlignment
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCColumnDCHorizontalAlignment(
    modifier: Modifier = Modifier,
    alignment: DCHorizontalAlignment?,
    onSelected: (DCHorizontalAlignment?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Start" to DCAlignment.Start,
        "CenterHorizontally" to DCAlignment.CenterHorizontally,
        "End" to DCAlignment.End
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = alignment,
        onSelected = onSelected
    )
}