package com.sefagurel.dynamiccomponent.webpanel.data

import com.sefagurel.dynamiccomponent.sharedmodel.DCBox
import com.sefagurel.dynamiccomponent.sharedmodel.DCColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyColumn
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCRow
import com.sefagurel.dynamiccomponent.sharedmodel.DCComponent

open class Scope(val componentList: List<DCComponent>?)

class BoxScope(
    boxComponent: DCBox
) : Scope(boxComponent.componentList)

class ColumnScope(
    columnComponent: DCColumn
) : Scope(columnComponent.componentList)

class LazyColumnScope(
    lazyColumnComponent: DCLazyColumn
) : Scope(lazyColumnComponent.componentList)

class RowScope(
    rowComponent: DCRow
) : Scope(rowComponent.componentList)

class LazyRowScope(
    lazyRowComponent: DCLazyRow
) : Scope(lazyRowComponent.componentList)