package com.sefagurel.dynamiccomponent.extension

import androidx.compose.foundation.clickable
import androidx.compose.foundation.interaction.MutableInteractionSource
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.composed
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.semantics.Role
import com.sefagurel.dynamiccomponent.sharedmodel.jsonKt
import kotlinx.serialization.json.JsonElement
import kotlinx.serialization.json.decodeFromJsonElement

internal inline fun <reified T> JsonElement.parse(): T {
    return jsonKt.decodeFromJsonElement(this)
}

internal inline fun <reified T> String.parse(): T {
    return jsonKt.decodeFromString(this)
}

internal fun String?.toColor(): Color? {
    if (this == null || this[0] != '#') return null
    var color = this.substring(1).toLong(16)
    if (this.length == 7) { // Set the alpha value
        color = color or -0x1000000
    } else {
        if (this.length != 9) return null
    }
    return Color(color.toInt())
}

internal val Int?.orZero: Int
    get() = this ?: 0

internal val Long?.orZero: Long
    get() = this ?: 0L

internal val Float?.orZero: Float
    get() = this ?: 0.0f

internal val Double?.orZero: Double
    get() = this ?: 0.0

internal fun Modifier.addModifier(
    predicate: Boolean? = true,
    modifier: Modifier.() -> Modifier,
): Modifier {
    return then(
        if (predicate == true) {
            modifier.invoke(Modifier)
        } else {
            Modifier
        }
    )
}

internal fun Modifier.clickableNoRipple(
    enabled: Boolean = true,
    onClickLabel: String? = null,
    role: Role? = null,
    onClick: () -> Unit
) = composed {
    clickable(
        indication = null,
        interactionSource = remember { MutableInteractionSource() },
        enabled = enabled,
        onClickLabel = onClickLabel,
        role = role,
        onClick = onClick
    )
}

internal fun Modifier.init() = this.then(object : Modifier.Element {})