package com.sefagurel.dynamiccomponent.sharedmodel

import kotlinx.serialization.Polymorphic
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
@Polymorphic
sealed interface DCComponent {
    val modifiers: List<DCModifier>?
}

@Serializable
@SerialName(Sn.Box)
data class DCBox(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.ContentAlignment)
    val contentAlignment: DCAlignment? = null,

    @SerialName(Sn.PropagateMinConstraints)
    val propagateMinConstraints: Boolean? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<DCComponent>? = null
) : DCComponent

@Serializable
@SerialName(Sn.Column)
data class DCColumn(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.VerticalArrangement)
    val verticalArrangement: DCVerticalArrangement? = null,

    @SerialName(Sn.HorizontalAlignment)
    val horizontalAlignment: DCHorizontalAlignment? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<DCComponent>? = null
) : DCComponent

@Serializable
@SerialName(Sn.LazyColumn)
data class DCLazyColumn(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.VerticalArrangement)
    val verticalArrangement: DCVerticalArrangement? = null,

    @SerialName(Sn.HorizontalAlignment)
    val horizontalAlignment: DCHorizontalAlignment? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<DCComponent>? = null
) : DCComponent

@Serializable
@SerialName(Sn.Row)
data class DCRow(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.HorizontalArrangement)
    val horizontalArrangement: DCHorizontalArrangement? = null,

    @SerialName(Sn.VerticalAlignment)
    val verticalAlignment: DCVerticalAlignment? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<DCComponent>? = null
) : DCComponent

@Serializable
@SerialName(Sn.LazyRow)
data class DCLazyRow(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.HorizontalArrangement)
    val horizontalArrangement: DCHorizontalArrangement? = null,

    @SerialName(Sn.VerticalAlignment)
    val verticalAlignment: DCVerticalAlignment? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<DCComponent>? = null
) : DCComponent

@Serializable
@SerialName(Sn.Text)
data class DCText(
    @SerialName(Sn.Text)
    val text: String? = null,

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.Color)
    val color: String? = null,

    @SerialName(Sn.FontSize)
    val fontSize: Float? = null,

    @SerialName(Sn.FontStyle)
    val fontStyle: DCFontStyle? = null,

    @SerialName(Sn.FontWeight)
    val fontWeight: Int? = null,

    @SerialName(Sn.FontFamily)
    val fontFamily: DCFontFamily? = null,

    @SerialName(Sn.LetterSpacing)
    val letterSpacing: Float? = null,

    @SerialName(Sn.TextDecoration)
    val textDecoration: DCTextDecoration? = null,

    @SerialName(Sn.TextAlign)
    val textAlign: DCTextAlign? = null,

    @SerialName(Sn.LineHeight)
    val lineHeight: Float? = null,

    @SerialName(Sn.Overflow)
    val overflow: DCTextOverflow? = null,

    @SerialName(Sn.SoftWrap)
    val softWrap: Boolean? = null,

    @SerialName(Sn.MaxLines)
    val maxLines: Int? = null,

    @SerialName(Sn.MinLines)
    val minLines: Int? = null
) : DCComponent

@Serializable
@SerialName(Sn.Image)
data class DCImage(
    @SerialName(Sn.Url)
    val url: String? = null,

    @SerialName(Sn.ContentDescription)
    val contentDescription: String? = null,

    @SerialName(Sn.Modifiers)
    override val modifiers: List<DCModifier>? = null,

    @SerialName(Sn.Alignment)
    val alignment: DCAlignment? = null,

    @SerialName(Sn.ContentScale)
    val contentScale: DCContentScale? = null,

    @SerialName(Sn.Alpha)
    val alpha: Float? = null
) : DCComponent