package com.sefagurel.dynamiccomponent.sharedmodel

import kotlinx.serialization.KSerializer
import kotlinx.serialization.descriptors.PrimitiveKind
import kotlinx.serialization.descriptors.PrimitiveSerialDescriptor
import kotlinx.serialization.descriptors.SerialDescriptor
import kotlinx.serialization.encoding.Decoder
import kotlinx.serialization.encoding.Encoder

// For DCHorizontalAlignment
object DCHorizontalAlignmentSerializer : KSerializer<DCHorizontalAlignment> {
    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("DCHorizontalAlignment", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: DCHorizontalAlignment) {
        when (value) {
            is DCAlignment.Start -> encoder.encodeString(Sn.Start)
            is DCAlignment.CenterHorizontally -> encoder.encodeString(Sn.CenterHorizontally)
            is DCAlignment.End -> encoder.encodeString(Sn.End)
        }
    }

    override fun deserialize(decoder: Decoder): DCHorizontalAlignment {
        return when (val value = decoder.decodeString()) {
            Sn.Start -> DCAlignment.Start
            Sn.CenterHorizontally -> DCAlignment.CenterHorizontally
            Sn.End -> DCAlignment.End
            else -> throw IllegalArgumentException("Unknown value: $value")
        }
    }
}

