// For DCAlignment
object DCAlignmentSerializer : KSerializer<DCAlignment> {
    override val descriptor: SerialDescriptor
        get() {
            return PrimitiveSerialDescriptor("DCAlignment", PrimitiveKind.STRING)
        }

    override fun serialize(encoder: Encoder, value: DCAlignment) {
        when (value) {
            is DCAlignment.TopStart -> encoder.encodeString(Sn.TopStart)
            is DCAlignment.TopCenter -> encoder.encodeString(Sn.TopCenter)
            is DCAlignment.TopEnd -> encoder.encodeString(Sn.TopEnd)
            is DCAlignment.CenterStart -> encoder.encodeString(Sn.CenterStart)
            is DCAlignment.Center -> encoder.encodeString(Sn.Center)
            is DCAlignment.CenterEnd -> encoder.encodeString(Sn.CenterEnd)
            is DCAlignment.BottomStart -> encoder.encodeString(Sn.BottomStart)
            is DCAlignment.BottomCenter -> encoder.encodeString(Sn.BottomCenter)
            is DCAlignment.BottomEnd -> encoder.encodeString(Sn.BottomEnd)
        }
    }

    override fun deserialize(decoder: Decoder): DCAlignment {
        return when (val value = decoder.decodeString()) {
            Sn.TopStart -> DCAlignment.TopStart
            Sn.TopCenter -> DCAlignment.TopCenter
            Sn.TopEnd -> DCAlignment.TopEnd
            Sn.CenterStart -> DCAlignment.CenterStart
            Sn.Center -> DCAlignment.Center
            Sn.CenterEnd -> DCAlignment.CenterEnd
            Sn.BottomStart -> DCAlignment.BottomStart
            Sn.BottomCenter -> DCAlignment.BottomCenter
            Sn.BottomEnd -> DCAlignment.BottomEnd
            else -> throw IllegalArgumentException("Unknown value: $value")
        }
    }
}