data class TreeNode(
    val id: String,
    val parentId: String?,
    val name: String,
    val isContainer: Boolean = false,
    val textColor: Color = Color.White,
    val backgroundColor: Color = Color.Transparent,
    val icon: ImageVector? = Icons.Default.CheckBoxOutlineBlank,
    val iconColor: Color = Color.White,
    val highlightColor: Color = Color.Red.copy(alpha = 0.5f),
    val onClick: (() -> Unit)? = null,
    var children: SnapshotStateList<TreeNode>? = null,
    var component: DCComponent? = null,
    var modifiers: List<DCComponentModifier>? = null,
)

data class DCComponentModifier(
    val id: String,
    val modifier: DCModifier
)

@Composable
fun TreeComponent(
    node: TreeNode? = null,
    selectedNode: TreeNode?,
    onClick: (ItemAction) -> Unit
) {
    if (node == null) return

    val localDensity = LocalDensity.current

    fun Int.toDp(): Dp {
        return (this / localDensity.density).dp
    }

    var minWidth by remember { mutableStateOf(0.dp) }

    Row(
        modifier = Modifier
            .fillMaxSize()
            .onGloballyPositioned { coordinates ->
                minWidth = coordinates.size.width.toDp()
            }
            .horizontalScroll(rememberScrollState())
            .background(Color.DarkGray)
    ) {
        TreeView(
            modifier = Modifier
                .fillMaxSize(),
            node = node,
            minWidth = minWidth,
            selectedNode = selectedNode,
            onClick = onClick
        )
    }
}

@Composable
fun TreeView(
    modifier: Modifier = Modifier,
    node: TreeNode,
    selectedNode: TreeNode?,
    minWidth: Dp? = null,
    onClick: (ItemAction) -> Unit
) {

    val expandedKeys = remember { mutableStateListOf<String>() }
    val selectedKeyState = remember { mutableStateOf<TreeNode?>(null) }

    LaunchedEffect(Unit) {
        expandedKeys.add(node.id)
    }

    LaunchedEffect(selectedNode) {
        if (selectedNode == null || selectedNode == selectedKeyState.value) return@LaunchedEffect
        selectedKeyState.value = selectedNode
        onClick.invoke(ItemAction.Select(selectedNode))
        if (!node.children.isNullOrEmpty()) {
            val parentId = selectedNode.parentId
            if (parentId != null && parentId !in expandedKeys) {
                expandedKeys.add(parentId)
            }
        }
    }

    LazyColumn(
        modifier = modifier
    ) {
        treeNodeItem(
            node = node,
            padding = 0.dp,
            onSelect = {
                val findedNode = findNodeById(node, it)
                selectedKeyState.value = findedNode
                onClick.invoke(ItemAction.Select(findedNode))
            },
            selectedKey = selectedKeyState.value?.id,
            onExpand = { key ->
                if (!node.children.isNullOrEmpty()) {
                    if (key !in expandedKeys) {
                        expandedKeys.add(key)
                    } else {
                        expandedKeys.remove(key)
                    }
                }
            },
            expandedKeys = expandedKeys,
            minWidth = minWidth,
            onClick = onClick
        )
    }
}

@OptIn(ExperimentalComposeUiApi::class)
private fun LazyListScope.treeNodeItem(
    node: TreeNode,
    padding: Dp,
    onSelect: (String) -> Unit,
    onExpand: (String) -> Unit,
    selectedKey: String?,
    expandedKeys: List<String>,
    minWidth: Dp? = null,
    onClick: (ItemAction) -> Unit
) {
    val paddingFactor = 16.dp
    val parentsKeys = getParentsKeys(node.id)
    val isVisible = if (parentsKeys.isEmpty()) {
        true
    } else {
        parentsKeys.all { it in expandedKeys }
    }

    if (isVisible) {
        item(node.id) {

            var isHovered by remember { mutableStateOf(false) }

            Row(
                modifier = Modifier
                    .then(
                        minWidth?.let {
                            Modifier.widthIn(min = minWidth)
                        } ?: run {
                            Modifier.fillMaxWidth()
                        }
                    )
                    .onPointerEvent(PointerEventType.Move) { isHovered = true }
                    .onPointerEvent(PointerEventType.Exit) { isHovered = false }
                    .height(28.dp)
                    .background(if (selectedKey == node.id) node.highlightColor else node.backgroundColor)
                    .clickable {
                        onSelect(node.id)
                        node.onClick?.invoke()
                    },
                verticalAlignment = Alignment.CenterVertically
            ) {
                (1..padding.value.toInt().div(paddingFactor.value.toInt())).forEach { _ ->
                    Spacer(Modifier.width(paddingFactor))
                    Spacer(
                        modifier = Modifier.width(1.dp)
                            .fillMaxSize()
                            .background(node.textColor.copy(alpha = 0.3f))
                    )
                }
                if (!node.children.isNullOrEmpty()) {
                    Icon(
                        imageVector = if (node.id in expandedKeys) Icons.Filled.KeyboardArrowDown else Icons.Filled.KeyboardArrowRight,
                        contentDescription = "",
                        tint = node.textColor,
                        modifier = Modifier.size(28.dp)
                            .padding(start = 5.dp)
                            .clickable {
                                onExpand(node.id)
                            }
                    )
                } else {
                    Spacer(Modifier.size(28.dp))
                }
                if (node.icon != null) {
                    Icon(
                        imageVector = node.icon,
                        contentDescription = "",
                        tint = node.iconColor,
                        modifier = Modifier.size(24.dp)
                    )
                } else {
                    Spacer(Modifier.size(24.dp))
                }

                Spacer(Modifier.size(8.dp))

                Text(
                    text = node.name,
                    fontSize = 15.sp,
                    fontWeight = FontWeight.Bold,
                    color = node.textColor,
                    maxLines = 1,
                    overflow = TextOverflow.Ellipsis,
                    modifier = Modifier
                )

                if (isHovered && node.isContainer) {
                    Spacer(Modifier.weight(1f))

                    Icon(
                        imageVector = Icons.Filled.CenterFocusStrong,
                        contentDescription = "",
                        tint = Color.Green,
                        modifier = Modifier.size(18.dp)
                            .clickable {
                                onClick.invoke(ItemAction.Focus(node))
                            }
                    )

                    Spacer(Modifier.width(16.dp))

                    Icon(
                        imageVector = Icons.Filled.AddCircle,
                        contentDescription = "",
                        tint = Color.Green,
                        modifier = Modifier.size(18.dp)
                            .clickable {
                                onClick.invoke(ItemAction.Add(node))
                            }
                    )

                    Spacer(Modifier.width(4.dp))

                    Icon(
                        imageVector = Icons.Filled.RemoveCircle,
                        contentDescription = "",
                        tint = Color.Red,
                        modifier = Modifier.size(18.dp)
                            .clickable {
                                onClick.invoke(ItemAction.Remove(node))
                            }
                    )

                    Spacer(Modifier.width(8.dp))
                }
            }
        }
    }
    if (!node.children.isNullOrEmpty()) {
        val childrenPadding = padding + paddingFactor
        node.children?.forEach {
            treeNodeItem(
                node = it,
                padding = childrenPadding,
                onSelect = onSelect,
                selectedKey = selectedKey,
                onExpand = onExpand,
                expandedKeys = expandedKeys,
                minWidth = minWidth,
                onClick = onClick
            )
        }
    }
}

sealed class ItemAction {
    data class Add(
        val treeNode: TreeNode?
    ) : ItemAction()

    data class Remove(
        val treeNode: TreeNode?
    ) : ItemAction()

    data class Select(
        val treeNode: TreeNode?
    ) : ItemAction()

    data class Focus(
        val treeNode: TreeNode?
    ) : ItemAction()
}

private fun getParentsKeys(childKey: String): List<String> {
    val parentsKeys = mutableListOf<String>()
    val splitKey = childKey.split(".")
    if (splitKey.size > 1) {
        (1..splitKey.size).forEach {
            if (it < splitKey.size) {
                parentsKeys.add(splitKey.dropLast(it).joinToString("."))
            } else {
                parentsKeys.add(splitKey[0])
            }
        }
    }
    return parentsKeys
}

fun findNodeById(
    root: TreeNode,
    targetId: String?
): TreeNode? {
    if (targetId == null) return null
    if (root.id == targetId) return root
    root.children?.forEach { child ->
        val result = findNodeById(child, targetId)
        if (result != null) return result
    }
    return null
}

fun updateNode(tree: TreeNode, updatedNode: TreeNode?): TreeNode? {
    if (updatedNode == null) return null
    if (tree.id == updatedNode.id) return updatedNode

    tree.children?.let { children ->
        for (index in children.indices) {
            val updatedChild = updateNode(children[index], updatedNode)
            if (updatedChild != null) {
                children[index] = updatedChild
                return tree.copy(children = children)
            }
        }
    }
    return null
}