import kotlinx.serialization.Serializable

@Serializable
data class DCFontWeight(val weight: Int) {

    companion object {
        val W100 = DCFontWeight(100)
        val W200 = DCFontWeight(200)
        val W300 = DCFontWeight(300)
        val W400 = DCFontWeight(400)
        val W500 = DCFontWeight(500)
        val W600 = DCFontWeight(600)
        val W700 = DCFontWeight(700)
        val W800 = DCFontWeight(800)
        val W900 = DCFontWeight(900)

        val Thin = W100
        val ExtraLight = W200
        val Light = W300
        val Normal = W400
        val Medium = W500
        val SemiBold = W600
        val Bold = W700
        val ExtraBold = W800
