fun createComponent(
    contentBuilder: (Scope.() -> Unit)? = null
): DCComponent {
    val componentList = arrayListOf<DCComponent>()
    contentBuilder?.invoke(Scope(componentList))
    if (componentList.size != 1) {
        throw IllegalStateException("ComposeContent must have only one component")
    }
    return componentList.first()
}

fun Scope.Box(
    modifier: Modifier? = null,
    contentAlignment: DCAlignment? = null,
    propagateMinConstraints: Boolean? = null,
    contentBuilder: (BoxScope.() -> Unit)? = null
) {
    val dcBox = DCBox(
        modifiers = modifier?.modifiers,
        contentAlignment = contentAlignment,
        propagateMinConstraints = propagateMinConstraints,
        componentList = arrayListOf()
    )

    contentBuilder?.invoke(BoxScope(dcBox))

    val child = dcBox.copy(
        modifiers = dcBox.modifiers?.takeIf { it.isNotEmpty() },
        componentList = dcBox.componentList?.takeIf { it.isNotEmpty() }
    )

    val children = this.componentList as? ArrayList
    children?.add(child)
}

