fun Scope.Text(
    text: String,
    modifier: Modifier? = null,
    color: DCColor? = null,
    fontSize: Float? = null,
    fontStyle: DCFontStyle? = null,
    fontWeight: DCFontWeight? = null,
    fontFamily: DCFontFamily? = null,
    letterSpacing: Float? = null,
    textDecoration: DCTextDecoration? = null,
    textAlign: DCTextAlign? = null,
    lineHeight: Float? = null,
    overflow: DCTextOverflow? = null,
    softWrap: Boolean? = null,
    maxLines: Int? = null,
    minLines: Int? = null
) {
    val dcText = DCText(
        text = text,
        modifiers = modifier?.modifiers,
        color = color?.value,
        fontSize = fontSize,
        fontStyle = fontStyle,
        fontWeight = fontWeight?.weight,
        fontFamily = fontFamily,
        letterSpacing = letterSpacing,
        textDecoration = textDecoration,
        textAlign = textAlign,
        lineHeight = lineHeight,
        overflow = overflow,
        softWrap = softWrap,
        maxLines = maxLines,
        minLines = minLines,
    )

    val child = dcText.copy(
        modifiers = dcText.modifiers?.takeIf { it.isNotEmpty() }
    )

    val children = this.componentList as? ArrayList
    children?.add(child)
}

