package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCAlignment
import com.sefagurel.dynamiccomponent.sharedmodel.DCArrangement
import com.sefagurel.dynamiccomponent.sharedmodel.DCVerticalArrangement
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCColumnDCVerticalArrangement(
    modifier: Modifier = Modifier,
    arrangement: DCVerticalArrangement?,
    onSelected: (DCVerticalArrangement?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Top" to DCArrangement.Top,
        "Bottom" to DCArrangement.Bottom,
        "SpacedByWithVerticalAlignment"
                to DCArrangement.SpacedByWithVerticalAlignment(0f, DCAlignment.Top),
        "AlignedVertical" to DCArrangement.AlignedVertical(DCAlignment.Top)
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = arrangement,
        onSelected = onSelected
    )
}