package com.sefagurel.dynamiccomponent.model

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.ui.Alignment
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.font.FontFamily
import androidx.compose.ui.text.font.FontStyle
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.text.style.TextDecoration
import androidx.compose.ui.text.style.TextOverflow
import androidx.compose.ui.unit.TextUnit

internal sealed interface ComponentUIModel

internal data class BoxUIModel(
    val modifier: List<Any>,
    val contentAlignment: Alignment,
    val propagateMinConstraints: Boolean,
    val children: List<ComponentUIModel>,
) : ComponentUIModel

internal data class ColumnUIModel(
    val modifier: List<Any>,
    val verticalArrangement: Arrangement.Vertical,
    val horizontalAlignment: Alignment.Horizontal,
    val children: List<ComponentUIModel>,
) : ComponentUIModel

internal data class LazyColumnUIModel(
    val modifier: List<Any>,
    val verticalArrangement: Arrangement.Vertical,
    val horizontalAlignment: Alignment.Horizontal,
    val children: List<ComponentUIModel>,
) : ComponentUIModel

internal data class RowUIModel(
    val modifier: List<Any>,
    val horizontalArrangement: Arrangement.Horizontal,
    val verticalAlignment: Alignment.Vertical,
    val children: List<ComponentUIModel>,
) : ComponentUIModel

internal data class LazyRowUIModel(
    val modifier: List<Any>,
    val horizontalArrangement: Arrangement.Horizontal,
    val verticalAlignment: Alignment.Vertical,
    val children: List<ComponentUIModel>,
) : ComponentUIModel

internal data class TextUIModel(
    val text: String,
    val modifier: List<Any>,
    val color: Color,
    val fontSize: TextUnit,
    val fontStyle: FontStyle? = null,
    val fontWeight: FontWeight? = null,
    val fontFamily: FontFamily? = null,
    val letterSpacing: TextUnit,
    val textDecoration: TextDecoration? = null,
    val textAlign: TextAlign? = null,
    val lineHeight: TextUnit,
    val overflow: TextOverflow,
    val softWrap: Boolean,
    val maxLines: Int,
    val minLines: Int,
) : ComponentUIModel

internal data class ImageUIModel(
    val url: String,
    val contentDescription: String?,
    val modifier: List<Any>,
    val alignment: Alignment,
    val contentScale: ContentScale,
    val alpha: Float
) : ComponentUIModel
