package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCTextDecoration
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCTextDCTextDecoration(
    modifier: Modifier = Modifier,
    value: DCTextDecoration?,
    onSelected: (DCTextDecoration?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "None" to DCTextDecoration.None,
        "Underline" to DCTextDecoration.Underline,
        "LineThrough" to DCTextDecoration.LineThrough
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = value,
        onSelected = onSelected
    )
}