package com.sefagurel.dynamiccomponent.sharedmodel

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable(DCHorizontalArrangementSerializer::class)
@SerialName(Sn.HorizontalArrangement)
sealed interface DCHorizontalArrangement

@Serializable(DCVerticalArrangementSerializer::class)
@SerialName(Sn.VerticalArrangement)
sealed interface DCVerticalArrangement

@Serializable(DCHorizontalOrVerticalArrangementSerializer::class)
@SerialName(Sn.HorizontalOrVerticalArrangement)
sealed interface DCHorizontalOrVerticalArrangement : DCHorizontalArrangement, DCVerticalArrangement

@Serializable(DCArrangementSerializer::class)
@SerialName(Sn.Arrangement)
sealed interface DCArrangement {

    @Serializable
    @SerialName(Sn.Start)
    object Start : DCHorizontalArrangement, DCArrangement

    @Serializable
    @SerialName(Sn.End)
    object End : DCHorizontalArrangement, DCArrangement

    @Serializable
    @SerialName(Sn.TopEnd)
    object TopEnd : DCArrangement

    @Serializable
    @SerialName(Sn.CenterStart)
    object CenterStart : DCArrangement

    @Serializable
    @SerialName(Sn.Top)
    object Top : DCVerticalArrangement, DCArrangement

    @Serializable
    @SerialName(Sn.Bottom)
    object Bottom : DCVerticalArrangement, DCArrangement

    @Serializable
    @SerialName(Sn.Center)
    object Center : DCHorizontalOrVerticalArrangement

    @Serializable
    @SerialName(Sn.SpaceEvenly)
    object SpaceEvenly : DCHorizontalOrVerticalArrangement

    @Serializable
    @SerialName(Sn.SpaceBetween)
    object SpaceBetween : DCHorizontalOrVerticalArrangement

    @Serializable
    @SerialName(Sn.SpaceAround)
    object SpaceAround : DCHorizontalOrVerticalArrangement

    @Serializable
    @SerialName(Sn.SpacedBy)
    data class SpacedBy(
        val space: Float? = null
    ) : DCHorizontalOrVerticalArrangement

    @Serializable
    @SerialName(Sn.SpacedByWithHorizontalAlignment)
    data class SpacedByWithHorizontalAlignment(
        val space: Float? = null,
        val alignment: DCHorizontalAlignment? = null
    ) : DCHorizontalArrangement

    @Serializable
    @SerialName(Sn.SpacedByWithVerticalAlignment)
    data class SpacedByWithVerticalAlignment(
        val space: Float? = null,
        val alignment: DCVerticalAlignment? = null
    ) : DCVerticalArrangement

    @Serializable
    @SerialName(Sn.AlignedHorizontal)
    data class AlignedHorizontal(
        val alignment: DCHorizontalAlignment? = null
    ) : DCHorizontalArrangement

    @Serializable
    @SerialName(Sn.AlignedVertical)
    data class AlignedVertical(
        val alignment: DCVerticalAlignment? = null
    ) : DCVerticalArrangement
}