import com.sefagurel.dynamiccomponent.webpanel.data.size
import com.sefagurel.dynamiccomponent.webpanel.data.verticalScroll
import com.sefagurel.dynamiccomponent.webpanel.data.weight

fun uiComponent() = createComponent {
    Column(
        modifier = Modifier().verticalScroll(),
        verticalArrangement = DCArrangement.Top,
        horizontalAlignment = DCAlignment.CenterHorizontally
    ) {
        repeat(5) {
            Row5()
            Row3()
            Column2()
            Column3()
            Row4()
        }
    }
}

private fun Scope.Row3() {
    LazyRow(
        modifier = Modifier()
            .fillMaxWidth()
            //.horizontalScroll(rememberScrollState())
            .padding(4f)
            .height(96f)
    ) {
        images.forEach {
            Image(
                url = it,
                modifier = Modifier()
                    .padding(4f)
                    .shadow(
                        shape = DCShape.CircleShape,
                        elevation = 4f,
                        ambientColor = DCColor.Black,
                        spotColor = DCColor.Black
                    )
                    .size(80f)
            )
        }
    }
}

private fun Scope.Column2() {
    Column(
        modifier = Modifier().fillMaxWidth().padding(4f)
    ) {
        images2.forEach {
            Image(
                url = it,
                modifier = Modifier()
                    .fillMaxWidth()
                    .padding(4f)
                    .clip(DCShape.RoundedCornerShape(8f, 8f, 8f, 8f))
                    .aspectRatio(2.285f)
            )
        }
    }
}

private fun Scope.Row4() {
    Row(
        modifier = Modifier()
            .fillMaxWidth()
            .padding(4f)
    ) {
        images3.forEach {
            Image(
                url = it,
                contentScale = DCContentScale.FillWidth,
                modifier = Modifier()
                    .weight(1f)
                    .padding(4f)
            )
        }
    }
}

private fun Scope.Column3() {
    Column(
        modifier = Modifier().padding(4f),
        horizontalAlignment = DCAlignment.Start
    ) {

        arrayListOf(rowData1, rowData2, rowData3).forEach {

            Row(
                modifier = Modifier()
            ) {
                it.forEach {

                    Column(
                        modifier = Modifier()
                            .padding(4f)
                            .shadow(
                                shape = DCShape.RoundedCornerShape(8f, 8f, 8f, 8f),
                                elevation = 4f,
                                ambientColor = DCColor.Black,
                                spotColor = DCColor.Black
                            )
                            .aspectRatio(1f)
                            .weight(1f)
                            .background(DCColor.White),
                        horizontalAlignment = DCAlignment.CenterHorizontally,
//                        verticalArrangement = DCArrangement.Center
                    ) {

                        Image(
                            url = it.second,
                            modifier = Modifier()
                                .fillMaxWidth()
                                .weight(0.7f)
                                .clip(DCShape.RoundedCornerShape(8f, 8f, 8f, 8f))
                                .padding(4f),
                            contentScale = DCContentScale.Crop
                        )

                        Text(
                            modifier = Modifier()
                                .weight(0.3f)
                                .clip(DCShape.RoundedCornerShape(8f, 8f, 8f, 8f))
                                .padding(4f)
                                .fillMaxWidth(),
                            text = it.first,
                            color = DCColor("#FF484848"),
                            fontSize = 11f,
                            textAlign = DCTextAlign.Center,
                            fontWeight = DCFontWeight.Bold,
                            maxLines = 2
                        )
                    }
                }
            }
        }
    }
}


private fun Scope.Row5() {
    Row(
        modifier = Modifier()
            .fillMaxWidth()
            .padding(4f)
    ) {

    }
}