import androidx.compose.ui.graphics.painter.Painter
import com.seiko.imageloader.rememberAsyncImagePainter

actual fun getScreenSizeDp(): ScreenSize = ScreenSize(360, 640)

actual fun getScreenSizePx(): ScreenSize = ScreenSize(360, 640)

