package com.sefagurel.dynamiccomponent.webpanel.data

import kotlinx.serialization.Serializable

@Serializable
data class DCColor(val value: String) {

    companion object {
        val Black = DCColor("#FF000000")
        val DarkGray = DCColor("#FF444444")
        val Gray = DCColor("#FF888888")
        val LightGray = DCColor("#FFCCCCCC")
        val White = DCColor("#FFFFFFFF")
        val Red = DCColor("#FFFF0000")
        val Green = DCColor("#FF00FF00")
        val Blue = DCColor("#FF0000FF")
        val Yellow = DCColor("#FFFFFF00")
        val Cyan = DCColor("#FF00FFFF")
        val Magenta = DCColor("#FFFF00FF")
        val Transparent = DCColor("#00000000")
    }
}