package com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.sefagurel.dynamiccomponent.sharedmodel.DCFontFamily
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.DropdownUI

@Composable
fun DCTextDCFontFamily(
    modifier: Modifier = Modifier,
    value: DCFontFamily?,
    onSelected: (DCFontFamily?) -> Unit
) {
    val items = listOf(
        "Select" to null,
        "Default" to DCFontFamily.Default,
        "SansSerif" to DCFontFamily.SansSerif,
        "Serif" to DCFontFamily.Serif,
        "Monospace" to DCFontFamily.Monospace,
        "Cursive" to DCFontFamily.Cursive
    )

    DropdownUI(
        modifier = modifier,
        items = items,
        selectedValue = value,
        onSelected = onSelected
    )
}