package com.sefagurel.dynamiccomponent.webpanel.component.settings.main

import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.width
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.dynamiccomponent.sharedmodel.DCLazyColumn
import com.sefagurel.dynamiccomponent.webpanel.component.TreeNode
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.TitleUI
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param.DCColumnDCHorizontalAlignment
import com.sefagurel.dynamiccomponent.webpanel.component.settings.other.param.DCColumnDCVerticalArrangement

@Composable
fun LazyColumnUI(
    node: TreeNode?,
    onNodeChanged: (TreeNode?) -> Unit
) {
    if (node == null) return

    val component: DCLazyColumn? = remember(node) { node.component as? DCLazyColumn }

    Column(
        modifier = Modifier
            .fillMaxWidth()
    ) {

        Row {
            Column(
                modifier = Modifier.weight(1f)
            ) {
                TitleUI("Vertical Arrangement")

                DCColumnDCVerticalArrangement(
                    arrangement = component?.verticalArrangement,
                    onSelected = {
                        val updatedNode =
                            node.copy(component = component?.copy(verticalArrangement = it))
                        onNodeChanged.invoke(updatedNode)
                    }
                )
            }
            Spacer(modifier = Modifier.width(16.dp))
            Column(
                modifier = Modifier.weight(1f)
            ) {
                TitleUI("Horizontal Alignment")

                DCColumnDCHorizontalAlignment(
                    alignment = component?.horizontalAlignment,
                    onSelected = {
                        val updatedNode =
                            node.copy(component = component?.copy(horizontalAlignment = it))
                        onNodeChanged.invoke(updatedNode)
                    }
                )
            }
        }
    }
}